@font-face {
  font-family: 'f-icon';
  /* project id 317880 */
  src: url("//at.alicdn.com/t/font_op4mzd9igslcjtt9.eot");
  src: url("//at.alicdn.com/t/font_op4mzd9igslcjtt9.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_op4mzd9igslcjtt9.woff") format("woff"), url("//at.alicdn.com/t/font_op4mzd9igslcjtt9.ttf") format("truetype"), url("//at.alicdn.com/t/font_op4mzd9igslcjtt9.svg#f-icon") format("svg");
}

html, body {
  margin: 0;
  height: 100%;
  width: 100%;
}

.abs {
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23) !important;
}

.box {
  width: 100%;
  height: 100%;
  display: flex;
  border: 1px solid #cdcdcd;
}

.pages {
  width: 200px;
  height: 100%;
  border-right: 1px solid #cdcdcd;
}

.workplace {
  flex: 1;
  background: #eee;
  position: relative;
  z-index: 1;
}

.attrF {
  padding: .3em;
  margin: .2em;
  font-size: 13px;
}

.attrF label {
  width: 100px;
  float: left;
  box-sizing: border-box;
  padding-left: .3em;
}

.attrF:after {
  clear: both;
  display: block;
  content: '';
}

.tools {
  width: 30px;
  border-right: 1px solid #cdcdcd;
  position: absolute;
  right: 0;
  top: 28px;
}

.attrs {
  width: 300px;
  border-left: 1px solid #cdcdcd;
  overflow: hidden;
  position: relative;
}

.attrs label {
  line-height: 27.5px;
}

.attrs input, .attrs select, .attrs textarea {
  box-shadow: 1px 1px 4px #ebebeb;
  border-radius: 3px;
  border: 1px solid #c2c2c2;
  outline: 0;
  padding: 5px;
}

.attrs input:focus, .attrs select:focus, .attrs textarea:focus {
  border: 1px solid #0C0;
}

.form {
  border-right: 1px solid #cdcdcd;
  border-radius: 3px;
  position: absolute;
  left: 0;
  right: 30px;
  top: 0;
  height: 90%;
  padding: .5em;
}

.form input, .form select, .form textarea {
  box-shadow: 1px 1px 4px #ebebeb;
  border-radius: 3px;
  border: 1px solid #c2c2c2;
  outline: 0;
  padding: 5px;
}

.form input:focus, .form select:focus, .form textarea:focus {
  border: 1px solid #0C0;
}

.act {
  box-shadow: #4fc3f7 0 0 10px;
}

.itemBox {
  position: absolute;
  right: -1px;
  top: 0;
  width: 30px;
  white-space: nowrap;
  overflow: hidden;
  height: 30px;
  transition: .3s ease-in-out;
  cursor: pointer;
  background: #fff;
  border: 1px solid #cdcdcd;
  border-right: 0;
  border-radius: 15px 0 0 15px;
}

.itemBox .icon {
  width: 30px;
  height: 30px;
  text-align: center;
  font-size: 16px;
}

.itemBox span {
  line-height: 30px;
  display: block;
  float: left;
}

.itemBox:hover {
  width: 100px;
}

.item {
  position: relative;
  user-select: none;
  font-size: 13px;
  height: 30px;
}

.fm-item {
  float: left;
  padding: .2em .5em 1em;
  margin: .2em;
  border-radius: 4px;
  background: #ffffff;
  min-height: 39.5px;
}

.fm-item [type="checkbox"] {
  margin-top: .6em;
}

.fm-item label {
  font-size: 13px;
  padding: 0 5px;
  display: inline-block;
  position: relative;
  line-height: 27.5px;
}

.fm-item * {
  float: left;
}

.design {
  overflow-x: visible;
  overflow-y: auto;
  margin: 1em;
  border: 1px solid #cdcdcd;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background: #fff;
}

.design.act {
  box-shadow: 0 3px 6px rgba(79, 195, 247, 0.16), 0 3px 6px rgba(79, 195, 247, 0.23);
}

.design .fm-item {
  transition: .2s ease-in-out;
  cursor: pointer;
}

.design .fm-item:hover {
  background: #f4f4f4;
}

.tabs {
  width: 100%;
  height: 30px;
}

.rq:after {
  content: '*';
  padding: 0 2px;
  color: #FF4136;
  position: absolute;
  left: -5px;
  top: 0;
}

.tab {
  position: relative;
  width: 120px;
  height: 30px;
  float: left;
  text-align: center;
  font-size: 13px;
  line-height: 30px;
  cursor: pointer;
}

.panelBox {
  position: absolute;
  transition: .3s;
  top: 30px;
  width: 200%;
  bottom: 0;
}

.err {
  width: 100%;
  font-size: 12px;
  color: #FF4136;
  line-height: 12px;
  height: 12px;
  display: block;
}

.panel {
  position: relative;
  float: left;
  width: 50%;
  height: 100%;
  overflow-y: auto;
}

.panel h3 {
  padding: 1em .5em .5em;
  margin: 0;
  font-size: 16px;
  border-top: 1px dashed #eee;
}

.tabAct {
  position: absolute;
  transition: .3s;
  left: 0;
  right: 0;
  bottom: 0;
  height: 2px;
  background: #32aede;
}

.rIpt {
  width: 50px;
}

.validator {
  padding: .5em;
  border-top: 1px dashed #eee;
  position: relative;
}

.validator .attrF {
  padding: 0;
  margin: .2em;
}

.validator:after {
  clear: both;
  display: block;
  content: '';
}

.rules {
  position: relative;
  float: left;
}

.rules:after {
  content: '';
  clear: both;
  display: block;
}

.addRule {
  color: #ffffff;
  text-align: center;
  font-size: 13px;
  display: block;
  float: left;
  margin: .5em;
  cursor: pointer;
  height: 24px;
  padding: 0 10px;
  line-height: 24px;
  background: #32aede;
  border-radius: 3px;
}

.addRule, .box, .attrs, .fm-item, .attrF, .tab, .panel, .pages, .tools, .form {
  box-sizing: border-box;
}

.close {
  color: #30a6d4;
  font-size: 12px;
  position: absolute;
  right: .5em;
  top: .5em;
  cursor: pointer;
}

.close:hover {
  color: #FF4136;
}

.clone {
  opacity: .5;
  pointer-events: none;
  outline: #30a6d4 dashed 2px;
}

.errF {
  outline: #f00 1px solid;
}

.mask {
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  opacity: 0;
  transition: .3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

.testFm {
  width: 480px;
  height: 800px;
  overflow-y: auto;
  position: relative;
  padding: 1em;
  border: 1px solid #cdcdcd;
  background: #fff;
}

.icon {
  font-family: 'f-icon' !important;
  color: #30a6d4;
}



