@font-face {
  font-family: 'f-icon';  /* project id 317880 */
  src: url('//at.alicdn.com/t/font_op4mzd9igslcjtt9.eot');
  src: url('//at.alicdn.com/t/font_op4mzd9igslcjtt9.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_op4mzd9igslcjtt9.woff') format('woff'),
  url('//at.alicdn.com/t/font_op4mzd9igslcjtt9.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_op4mzd9igslcjtt9.svg#f-icon') format('svg');
}

$pageW: 200px;
$toolW: 30px;
$attrW: 300px;
$border: 1px solid #cdcdcd;
$tabH: 30px;
$tabW: 120px;
$red: #FF4136;

@mixin clear() {
  &:after {
    clear: both;
    display: block;
    content: '';
  }
}

html, body {
  margin: 0;
  height: 100%;
  width: 100%;
}

.abs {
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23) !important;
}

.box {
  width: 100%;
  height: 100%;
  display: flex;
  border: $border;
}

.pages {
  width: $pageW;
  height: 100%;
  border-right: $border;
}

.workplace {
  flex: 1;
  background: #eee;
  position: relative;
  z-index: 1;
}

.attrF {
  padding: .3em;
  margin: .2em;
  font-size: 13px;
  label {
    width: 100px;
    float: left;
    box-sizing: border-box;
    padding-left: .3em;
  }
  @include clear()
}

.tools {
  width: $toolW;
  border-right: $border;
  position: absolute;
  right: 0;
  top: 28px;
}

.attrs {
  width: $attrW;
  border-left: $border;
  overflow: hidden;
  position: relative;
  label{
    line-height: 27.5px;
  }
  input,select,textarea{
    box-shadow: 1px 1px 4px #ebebeb;
    border-radius: 3px;
    border: 1px solid #c2c2c2;
    outline: 0;
    padding: 5px;
    &:focus{
      border: 1px solid #0C0;
    }
  }
}

.form {
  border-right: $border;
  border-radius: 3px;
  position: absolute;
  left: 0;
  right: $toolW;
  top: 0;
  height: 90%;
  padding: .5em;
  input,select,textarea{
    box-shadow: 1px 1px 4px #ebebeb;
    border-radius: 3px;
    border: 1px solid #c2c2c2;
    outline: 0;
    padding: 5px;
    &:focus{
      border: 1px solid #0C0;
    }
  }
}
.act{
  box-shadow: #4fc3f7 0 0 10px;
}
.itemBox {
  position: absolute;
  right: -1px;
  top: 0;
  width: $toolW;
  white-space: nowrap;
  overflow: hidden;
  height: 30px;
  transition: .3s ease-in-out;
  cursor: pointer;
  background: #fff;
  border: $border;
  border-right: 0;
  border-radius: 15px 0 0 15px;
  .icon {
    width: $toolW;
    height: $toolW;
    text-align: center;
    font-size: 16px;
  }
  span {
    line-height: $toolW;
    display: block;
    float: left;
  }
  &:hover {
    width: 100px;
  }
}

.item {
  position: relative;
  user-select: none;
  font-size: 13px;
  height: $toolW;
}

.fm-item {
  float: left;
  padding: .2em .5em 1em;
  margin: .2em;
  border-radius: 4px;
  background: #ffffff;
  min-height: 39.5px;
  [type="checkbox"]{
    margin-top: .6em;
  }
  label {
    font-size: 13px;
    padding: 0 5px;
    display: inline-block;
    position: relative;
    line-height: 27.5px
  }
  *{
    float: left;
  }
}

.design {
  overflow-x: visible;
  overflow-y: auto;
  margin: 1em;
  border: $border;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  &.act{
    box-shadow: 0 3px 6px rgba(79,195,247,0.16), 0 3px 6px rgba(79,195,247,0.23);
  }
  background: #fff;
  .fm-item {
    transition: .2s ease-in-out;
    cursor: pointer;
    &:hover {
      background: #f4f4f4;
    }
  }
}

.tabs {
  width: 100%;
  height: $tabH;
}

.rq {
  &:after {
    content: '*';
    padding: 0 2px;
    color: $red;
    position: absolute;
    left: -5px;
    top: 0;
  }
}

.tab {
  position: relative;
  width: $tabW;
  height: $tabH;
  float: left;
  text-align: center;
  font-size: 13px;
  line-height: $tabH;
  cursor: pointer;
}

.panelBox {
  position: absolute;
  transition: .3s;
  top: $tabH;
  width: 200%;
  bottom: 0;
}

.err {
  width: 100%;
  font-size: 12px;
  color: $red;
  line-height: 12px;
  height: 12px;
  display: block;
}

.panel {
  position: relative;
  float: left;
  width: 50%;
  height: 100%;
  overflow-y: auto;
  h3 {
    padding: 1em .5em .5em;
    margin: 0;
    font-size: 16px;
    border-top: 1px dashed #eee;
  }
}

.tabAct {
  position: absolute;
  transition: .3s;
  left: 0;
  right: 0;
  bottom: 0;
  height: 2px;
  background: #32aede;
}

.rIpt {
  width: 50px;
}

.validator {
  .attrF {
    padding: 0;
    margin: .2em;
  }
  padding: .5em;
  border-top: 1px dashed #eee;
  position: relative;
  @include clear()
}

.range {

}

.rules {
  position: relative;
  float: left;
  &:after {
    content: '';
    clear: both;
    display: block;
  }
}

.addRule {
  color: #ffffff;
  text-align: center;
  font-size: 13px;
  display: block;
  float: left;
  margin: .5em;
  cursor: pointer;
  height: 24px;
  padding: 0 10px;
  line-height: 24px;
  background: #32aede;
  border-radius: 3px;
}

.addRule, .box, .attrs, .fm-item,.attrF, .tab, .panel, .pages, .tools, .form {
  box-sizing: border-box;
}

.close {
  color: #30a6d4;
  font-size: 12px;
  position: absolute;
  right: .5em;
  top: .5em;
  cursor: pointer;
  &:hover {
    color: $red;
  }
}

.clone {
  opacity: .5;
  pointer-events: none;
  outline: #30a6d4 dashed 2px;
}

.errF {
  outline: #f00 1px solid;
}

//fortest
.mask {
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .4);
  opacity: 0;
  transition: .3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

.testFm {
  width: 480px;
  height: 800px;
  overflow-y: auto;
  position: relative;
  padding: 1em;
  border: $border;
  background: #fff;
}

.icon {
  font-family: 'f-icon' !important;
  color: #30a6d4;
}


